import React from 'react';
import { Box } from '@mui/material';
import Header from '../components/Header/Header';
import GlobalHeader from '../components/Header/GlobalHeader';
import Footer from '../components/Footer/Footer';
import ProductList from '../components/productSearchComponents/ProductList';
import { useLocation } from 'react-router-dom';
import ProductBreadcrumbs from '../components/BackButton'; //  Breadcrumbs 

const SearchPage = () => {
  const location = useLocation();
  const params = new URLSearchParams(location.search);
  const searchTerm = params.get('query') || ''; //  query 

  return (
    <Box>
      <Header />
      <GlobalHeader />
      <ProductBreadcrumbs category="Searching Page" />{' '}
      {/*  'Searching Page'  */}
      {/*  searchTerm  SearchBar */}
      <ProductList searchTerm={searchTerm} />
      <ProductBreadcrumbs category="Searching Page" /> <Footer />
    </Box>
  );
};

export default SearchPage;
