import React from 'react';
import { Card, CardContent, CardMedia } from '@mui/material';
import { Product } from '../../types/types';
import { BodyText, PriceTag } from '../../theme/customStyles';

//set the api
interface HomePageProductCardProps {
  product: Product;
  onClick: (product: Product) => void; //  onClick 
}

const HomePageProductCard: React.FC<HomePageProductCardProps> = ({
  product,
  onClick,
}) => {
  return (
    <Card
      sx={{
        width: '240px',
        height: '336px',
        borderRadius: '8px',
        mb: 2,
        overflow: 'hidden',
        cursor: 'pointer',
      }}
      // onClickproductrandomImage
      onClick={() => onClick(product)}
    >
      <CardMedia
        component="img"
        height="240px"
        width="240px"
        image={`${process.env.PUBLIC_URL}/${product.image}`} // 
        alt={product.name}
        sx={{
          borderRadius: '8px 8px 8px 8px',
        }}
      />
      <CardContent sx={{ padding: '0px', marginTop: '8px' }}>
        <BodyText
          style={{
            marginBottom: '0.5rem',
            whiteSpace: 'nowrap', // 
            overflow: 'hidden', // 
            textOverflow: 'ellipsis', // 
            color: 'black',
          }}
        >
          {product.name} {/*  */}
        </BodyText>
        <BodyText style={{ color: '#595959', marginTop: '2px' }}>
          Quantity: {product.quantity} {/*  */}
          {product.inStock ? 'In Stock' : 'Out of Stock'} {/*  */}
        </BodyText>
        <PriceTag style={{ marginTop: '4px' }}>
          ${product.price} {/*  */}
        </PriceTag>
      </CardContent>
    </Card>
  );
};

export default HomePageProductCard;
